import React from "react";
import arr from "./objects/Question_array.js";
import { Link } from "react-router-dom";
import "./styles.css";

function navigation_buttons(val) {
  return (
    <>
      <Link class="nav-btn" to={`/${val}`}>
        {val}
      </Link>
    </>
  );
}

export default function Navigation() {
  return (
    <div className="Question_pallete">
      <form>{arr.map(navigation_buttons)}</form>
    </div>
  );
}
